@tailwind base;
@tailwind components;
@tailwind utilities;

/* 自定义样式 */
.human-msg {
  background-color: #fffde7; /* Light yellow for human messages */
}
.claude-msg {
  background-color: #e3f2fd; /* Light blue for Claude messages */
}
.selected-msg {
  background-color: #bbdefb !important; /* Darker blue for selected messages */
}

/* --- Global Search Highlighting Style --- */
/* Single style for all matches found by the global search */
.global-search-highlight {
  background-color: rgba(255, 236, 179, 0.9); /* Amber 100, slightly less transparent */
  color: #212121; /* Dark gray text */
  padding: 1px 2px;
  margin: -1px -2px;
  border-radius: 3px;
  box-shadow: 0 0 0 1px rgba(255, 202, 40, 0.8); /* Amber 400 border */
  /* Removed transition for simplicity, can be added back if needed */
}

/* Removed .current-match style as specific match highlighting within detail is now handled differently */

/* --- End Global Search Styles --- */


/* 修复布局问题 */
.detail-content-container {
  width: 100%;
  height: 100%;
  overflow: auto !important; /* Ensure scrolling within the container */
}

.json-content {
  width: 100%;
  overflow-x: auto; /* Allow horizontal scroll for wide JSON */
}

/* 确保主要内容区域不会被挤压 */
.flex-1 {
  flex: 1 1 0% !important;
}

/* 确保消息列表面板有一个固定的最小宽度 */
.message-list-panel {
  min-width: 320px;
  width: 40%; /* Adjust as needed */
  max-width: 500px; /* Adjust as needed */
  flex-shrink: 0; /* Prevent shrinking */
}

/* 使用媒体查询调整竖屏模式的消息列表宽度 */
@media (orientation: portrait) {
  .message-list-panel {
    min-width: 280px;
    width: 30%; /* Adjust for portrait */
  }
}

/* 内容面板样式增强 */
.content-panel {
  min-width: 0; /* Allow shrinking */
  flex: 1 1 auto; /* Allow growing and shrinking */
  position: relative; /* For absolute positioning of children */
}

/* 消息详情区绝对定位容器 */
.content-panel > div:first-child {
  position: absolute;
  inset: 0;
  overflow: auto !important; /* Critical for scrolling */
  transition: all 0.3s ease-in-out;
  /* 确保内容可以完整显示，不被底部面板遮挡 */
  display: flex;
  flex-direction: column;
}

/* 底部面板过渡动画 */
.content-panel > div:last-child {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  transition: all 0.3s ease-in-out;
  border-top: 1px solid #e5e7eb; /* Tailwind gray-200 */
  background: white;
}

/* JSON选项卡的特殊处理 */
.json-tab-content {
  max-width: 100%;
  overflow-x: auto !important;
  overflow-y: auto !important;
}

/* 提高JSON代码块的可读性 */
pre[class*="language-json"] {
  white-space: pre-wrap !important; /* Allow wrapping */
  word-break: break-word !important; /* Break long words */
  max-width: 100% !important;
}

/* 修复选项卡样式 */
.tab-button {
  flex-shrink: 0; /* Prevent tabs from shrinking */
}

/* 解决某些界面元素的固定宽度问题 */
.fixed-width-input {
  width: auto !important;
  flex: 1; /* Allow input to grow */
}

/* 确保代码高亮区域的限制 */
.syntax-highlighter-container {
  max-width: 100%;
  overflow-x: auto;
}

/* 确保所有标签面板具有一致的最小高度 */
.tab-content-container {
  min-height: 300px; /* Ensure consistent height */
  display: flex;
  flex-direction: column;
}

/* 设置所有内容区域统一样式 */
.panel-content {
  flex: 1; /* Allow growing */
  overflow-y: auto;
  padding: 1rem;
  background: white;
  border: 1px solid #e5e7eb; /* Tailwind gray-200 */
  border-top: none;
  border-radius: 0 0 0.25rem 0.25rem; /* Rounded bottom corners */
}

/* 确保内部内容不会导致高度变化 */
.metadata-panel,
.export-panel,
.tools-panel {
  min-height: 300px;
  display: flex;
  flex-direction: column;
}

/* 底部面板容器 - 添加过渡动画 */
.panel-content-container {
  height: 288px; /* Fixed height for content area */
  transition: height 0.3s ease-in-out;
  overflow: auto; /* Scroll within this container */
}

/* 各个面板内容区域统一样式 */
.panel-content-area {
  min-height: 260px; /* Ensure minimum height */
}

/* 修复分支管理器样式 */
.branch-manager {
  border-top: 1px solid #e5e7eb; /* Tailwind gray-200 */
  padding: 0.5rem;
  background-color: #f9fafb; /* Tailwind gray-50 */
}

/* 添加底部状态栏样式 */
.status-bar {
  background-color: #f9fafb; /* Tailwind gray-50 */
  border-top: 1px solid #e5e7eb; /* Tailwind gray-200 */
  padding: 0.5rem 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* 底部工具栏的收起/展开动画 */
.bottom-panel-toggle {
  transition: transform 0.2s ease;
}

.bottom-panel-toggle.collapsed {
  transform: rotate(180deg);
}

/* 底部选项卡导航栏 */
.bottom-tabs-nav {
  display: flex;
  background-color: #f9fafb; /* Tailwind gray-50 */
  border-bottom: 1px solid #e5e7eb; /* Tailwind gray-200 */
}

/* 底部快捷按钮区域 */
.bottom-action-buttons {
  display: flex;
  align-items: center;
  gap: 0.5rem; /* Space between buttons */
}

/* 收起面板时的状态栏样式增强 */
.status-bar-expanded {
  padding: 0.75rem 1rem;
}

/* 修复面板显示/隐藏时的布局跳动 */
.main-content-wrapper {
  display: flex;
  flex-direction: column;
  flex: 1;
  overflow: hidden; /* Prevent main wrapper from scrolling */
}

/* 添加更多的overflow控制，确保滚动正常工作 */
.content-panel div[class*='overflow-auto'] {
  overflow: auto !important;
}

/* 消息列表面板滚动修复 */
.message-list-panel > div:last-child {
  overflow: auto !important;
}

/* 底部面板内容滚动修复 */
.content-panel > div:last-child > div:last-child {
  overflow: auto !important;
}

/* 全局修复滚动问题 */
div[class*='overflow-auto'],
div[class*='overflow-y-auto'] {
  overflow: auto !important;
}

/* Markdown specific styles */
.markdown-content h2 {
  font-size: 1.25rem; /* text-xl */
  font-weight: 700; /* font-bold */
  color: #1d4ed8; /* text-blue-700 */
  border-bottom: 1px solid #bfdbfe; /* border-blue-200 */
  padding-bottom: 0.25rem; /* pb-1 */
  margin-top: 1.25rem; /* mt-5 */
  margin-bottom: 0.75rem; /* mb-3 */
}

.markdown-content h3 {
  font-size: 1.125rem; /* text-lg */
  font-weight: 600; /* font-semibold */
  color: #2563eb; /* text-blue-600 */
  margin-top: 1rem; /* mt-4 */
  margin-bottom: 0.5rem; /* mb-2 */
}

.markdown-content p {
    margin-bottom: 0.75rem; /* Add some space below paragraphs */
    line-height: 1.6; /* Improve readability */
}

.markdown-content ul, .markdown-content ol {
    margin-left: 1.5rem; /* Indent lists */
    margin-bottom: 1rem;
}

.markdown-content li {
    margin-bottom: 0.25rem;
}

.markdown-content code:not([class*="language-"]) {
    background-color: #f3f4f6; /* bg-gray-100 */
    color: #ef4444; /* text-red-500 */
    padding: 0.1rem 0.3rem;
    border-radius: 0.25rem;
    font-size: 0.9em;
}

.markdown-content pre {
    margin-bottom: 1rem;
    border-radius: 0.375rem; /* rounded-md */
    overflow: auto; /* Add scroll for long code blocks */
}

/* Ensure message detail scroll works */
.message-detail-scroll-container {
    flex: 1 1 auto;
    overflow-y: auto;
    position: relative; /* Needed for potential absolute positioned children */
}
