.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* 在您的App.css或其他CSS文件中添加以下样式 */

/* 全局搜索高亮样式 */
.global-search-highlight {
  background-color: #ffeb3b;
  border-radius: 2px;
  padding: 0 2px;
  font-weight: 500;
  position: relative;
  display: inline;
  box-shadow: 0 0 0 1px rgba(255, 235, 59, 0.5);
}

/* 对当前活动高亮的特殊样式 */
.global-search-highlight.active {
  background-color: #ff9800;
  color: white;
  box-shadow: 0 0 0 1px rgba(255, 152, 0, 0.5);
}

/* 搜索相关按钮的样式 */
button.search-nav-button {
  transition: background-color 0.2s, transform 0.1s;
}

button.search-nav-button:active {
  transform: scale(0.95);
}

/* 搜索结果悬浮栏动画 */
.search-controls-enter {
  opacity: 0;
  transform: translateY(-10px);
}

.search-controls-enter-active {
  opacity: 1;
  transform: translateY(0);
  transition: opacity 0.2s, transform 0.2s;
}

.search-controls-exit {
  opacity: 1;
  transform: translateY(0);
}

.search-controls-exit-active {
  opacity: 0;
  transform: translateY(-10px);
  transition: opacity 0.2s, transform 0.2s;
}

/* 搜索快捷键提示样式 */
kbd {
  font-family: 'SFMono-Regular', Consolas, 'Liberation Mono', Menlo, monospace;
  display: inline-block;
  padding: 0.1em 0.4em;
  font-size: 0.85em;
  line-height: 1.4;
  color: #444;
  vertical-align: middle;
  background-color: #f7f7f7;
  border: solid 1px #ccc;
  border-radius: 3px;
  box-shadow: inset 0 -1px 0 #bbb;
}     

/* 添加到 App.css */
.status-message {
  max-width: 300px; /* 设置最大宽度 */
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  display: inline-block; /* 确保单行显示和截断生效 */
}

/* 响应式调整 */
@media (max-width: 768px) {
  .status-message {
    max-width: 150px; /* 在小屏幕上使用更小的最大宽度 */
  }
}